@font-face {
  font-family: "Oswald";
  src: local("Oswald"), local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Oswald";
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

*, *:before, *:after {
  box-sizing: border-box; }

body {
  width: 100%;
  height: 100%;
  background-color: #f8f9fc; }

.header {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; }

.site-menu {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0;
  flex-wrap: wrap; }

.site-menu__item {
  list-style: none; }

.site-menu__link {
  display: block;
  text-decoration: none;
  text-align: center;
  margin: 2rem;
  padding: 1rem 2rem;
  font-size: 1rem;
  border-radius: 99px;
  background-color: #1d1d1f;
  color: #ffffff;
  border: none;
  cursor: pointer; }

.site-menu__link:hover,
.site-menu__link:active {
  color: #5e636e;
  opacity: 0.5; }

.browser__button {
  width: 100%;
  height: 50px;
  margin: 20px 0;
  background-color: #ffffff;
  color: #222222;
  border: none;
  cursor: pointer; }

.browser__table {
  margin: 20px; }

th, td {
  border: none;
  text-align: center;
  padding: 5px; }

tr {
  height: 10px;
  border: none; }

table {
  border: none;
  empty-cells: hide;
  text-align: center; }

tbody tr:nth-child(odd) {
  background-color: #ffffff; }

tbody tr:nth-child(even) {
  background-color: #f8f9fc; }

thead th, tfoot th, tfoot td {
  background: linear-gradient(to bottom, #ffffff, #f8f9fc); }

.background-image {
  background-image: url("../../img/cat.jpeg");
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover; }

li {
  list-style: none; }

ul {
  padding: 0; }

.library {
  margin: 5rem 10rem; }

form {
  width: 30%; }

input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06); }

select {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 10px;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06); }

button {
  appearance: none;
  padding: 10px;
  border: none;
  background-color: #1d1d1f;
  color: #ffffff;
  font-weight: 600;
  border-radius: 5px;
  width: 100%; }

.error::after {
  background-color: #cc4d33;
  color: papayawhip;
  font-size: 1rem;
  line-height: 1.8;
  width: 150px;
  height: 50px;
  padding-left: 1rem;
  position: absolute;
  left: 0;
  content: attr(data-errormsg);
  text-align: left; }

table, td, th {
  border-collapse: collapse;
  margin: 40px 0; }
